<template lang="en">
<div>
    <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
    <div class="row placeholder">
        <div class="col-md-12 p-0">
            <ReportViewer v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../service/DataService';
import ReportViewer from './ReportViewer.vue';
export default {
    components: {
        ReportViewer
    },
    data() {
        return {
            reportPath: null,
            parameter: [],
            FindalLocationArray: [],
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                        astype: "TreeSelectField",
                        label: this.$t('Location'),
                        name: "Location",
                        value: null,
                        placeholder: "Select Location",
                        validationRules: {
                            "required": true,
                            max: 250
                        },
                        config: {
                            options: [],
                            onChange: () => {}

                        },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('ProductType'),
                        name: "Product Type",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [],

                        },

                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Status'),
                        name: "Status",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [{
                                    name: 'Active',
                                    value: 1001
                                },
                                {
                                    name: 'InActive',
                                    value: 1002
                                },
                                {
                                    name: 'On Hold  ',
                                    value: 1003
                                },
                                {
                                    name: 'Reserved',
                                    value: 1004
                                },
                                {
                                    name: 'Retired',
                                    value: 1005
                                },
                                {
                                    name: 'Sold',
                                    value: 1006
                                },
                            ],
                            onChange: () => {}

                        },
                    },

                    {
                        astype: "DateField",
                        label: this.$t('DeployDate'),
                        name: "Deploy Date",
                        value: '',
                        placeholder: "10/12/2022 12:AM",

                        config: {
                            format: "M/D/YYYY"
                        }

                    },
                    {
                        astype: "DateField",
                        label: this.$t('DeployDateEnd'),
                        name: "Date Date End",
                        value: '',
                        placeholder: "10/12/2022 12:AM",

                        config: {
                            format: "M/D/YYYY"
                        }

                    },

                ]
            }],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-search pr-2'></i>Search",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Clear all",
                    onClick: this.ClearAll

                }
            ],
            DataLoaded: 0

        }

    },
    created: async function () {
        var vm = this;
        await vm.GetLocationByCompanyId();
        await vm.BindItemTypeByUserId();
        vm.DataLoaded = 1;
    },
    methods: {
        GetLocationByCompanyId: async function () {
            var vm = this;
            var url = `reqForm=`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                (vm.FormSchema)[0].Data[0].config.options = vm.FindalLocationArray;
            });
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        async BindItemTypeByUserId() {

            let url = `type=&issueTo=&locationId=&requestType=ASSIGN&requestFrom=`;
            var vm = this;
            await DataService.AssignGetAssetType(url).then(response => {
                response.data.forEach((itm, index) => {
                    (vm.FormSchema)[0].Data[1].config.options.push({
                        name: itm.ASSETTYPE,
                        value: itm.ASSETID
                    })

                })
            });
        },
        async onSubmit() {
            var vm = this;
            let myObj = {
                reportName: "assetcountreport",
                Location: (vm.FormSchema)[0].Data[0].value,
                AssetTypesIds: (vm.FormSchema)[0].Data[1].value,
                AssetStatus: (vm.FormSchema)[0].Data[2].value,
                dateFrom: (vm.FormSchema)[0].Data[3].value,
                dateTo: (vm.FormSchema)[0].Data[4].value,

            }
            await DataService.ReportMain(myObj).then(function (response) {

                console.log(response);
                vm.reportPath = response.data.reportpath;
                vm.parameter = response.data.parameters;
            })

        },
        ClearAll: function () {
            var vm = this;
            (vm.FormSchema)[0].Data[0].value = null;
            (vm.FormSchema)[0].Data[1].value = '';
            (vm.FormSchema)[0].Data[2].value = '';
            (vm.FormSchema)[0].Data[3].value = '';
            (vm.FormSchema)[0].Data[4].value = '';
            vm.parameter= []; 

        },
    }
};
</script>
