<template>
  <div>
    <dynamic-form
      lang="en"
      v-if="DataLoaded"
      :buttons="buttons"
      :schema="FormSchema"
      v-on:OnSubmit="onSubmit"
    ></dynamic-form>

    <div class="row placeholder">
        <div class="col-md-12 p-0">
            <ReportViewer  v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
        </div>
    </div>
  </div>
        
</template>

<script>
import DataService from "../../service/DataService";
import ReportViewer from './ReportViewer.vue';  
export default {
  components: { ReportViewer },
  data() {
    return {
      IsuuedArray:[],
      reportPath: null,
      parameter: [],
      FindalLocationArray:[],
      FormSchema: [
        {
          layoutType: "triple",
          Data: [
            {
              astype: "TreeSelectField",
              label: this.$t("Location"),
              name: "Location",
              value: null,
              placeholder: "Select Location",             
              config: {
                options: [],
              },
              validationRules: { required: true, max: 250 },
            },
            {
              astype: "SelectField",
              label: this.$t("ProductType"),
              name: "ProductType",
              value: "",
              placeholder: "",

              config: {
                options: [],
                onChange: this.onChangeDropdown,
              },
            },
            {
              astype: "MultiSelectField",
              label: this.$t("ProductName"),
              name: "Product Name",
              value: "",
              placeholder: "",
              mode: "tag",
              config: {
                options: [],               
              },
            },
            {
              astype: "SelectField",
              label: this.$t("Status"),
              name: "Status",
              value: "",
              placeholder: "",
              config: {
                options: [
                  { name: "Not Returned", value: '0' },
                  { name: "Returned", value: '1' },
                ],
                onChange: () => {},
              },
            },
            {
              astype: "SelectField",
              label: this.$t("IssuedTo"), 
              name: "IssuedTo",
              value: "",
              placeholder: "",
              config: {
                options: [],
              },
            },  
            {
              astype: "TextField",
              label: this.$t("IssuedBy"),
              name: "IssuedBy",
              value: "",
              placeholder: "",
              config: {},
            },
            {
              astype: "DateField",
              label: this.$t("IssueDate"),
              name: "IssueDate",
              value: "",
              placeholder: "10/12/2022 12:AM",

              config: { format: "M/D/YYYY" },
            },
            {
              astype: "DateField",
              label: this.$t("IssueDateEnd"),
              name: "IssueDateEnd",
              value: "",
              placeholder: "10/12/2022 12:AM",

              config: { format: "M/D/YYYY" },
            },
            {
              astype: "DateField",
              label: this.$t("ReturnDate"),
              name: "ReturnDate",
              value: "",
              placeholder: "10/12/2022 12:AM",

              config: { format: "M/D/YYYY" },
            },
            {
              astype: "DateField",
              label: this.$t("ReturnDateEnd"),
              name: "ReturnDateEnd",
              value: "",
              placeholder: "10/12/2022 12:AM",

              config: { format: "M/D/YYYY" },
            },
          ],
        },
      ],
      buttons: [
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-search pr-2'></i>Search",
        },

        {
          type: "button",
          class: "btn btn-danger",
          text: "<i class='fa fa-close pr-2'></i>Clear all",
          onClick: this.ClearAll
        },
      ],
        DataLoaded:0
    };
  },
  created: async function () {
    var vm = this;
    await vm.GetLocationByCompanyId();
    await vm.BindItemTypeByUserId();
     await vm.GetIssuedDetails();
   vm.DataLoaded=1;
  },
  methods: {
    GetLocationByCompanyId: async function () {
        var vm = this;
        var url = `reqForm=`
        await DataService.GetLocationByuserId(url).then(response => {
            vm.FindalLocationArray = [];
            response.data.data.forEach((value) => {
                var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist.length == 0) {
                    if (value.PARENT_LOCATION_ID == null) {
                        let locationId = value.COMPANY_LOCATION_ID
                        let rslt = vm.getChildLocation(locationId, value, response.data.data)
                        vm.FindalLocationArray.push(rslt)
                    }
                }
            });
            (vm.FormSchema)[0].Data[0].config.options = vm.FindalLocationArray;
        });
    },

    getChildLocation: function (locationId, currentItem, data) {
        let ths = this;
        let treeArray = {};
        treeArray = {
            id: currentItem.COMPANY_LOCATION_ID,
            label: currentItem.LOCATION_NAME
        }
        let retrunArray = treeArray;
        var childArray = data.filter(function (el) {
            return el.PARENT_LOCATION_ID == locationId;
        });
        let finalChildArray = [];
        childArray.forEach((value) => {
            let ChildfolderId = value.COMPANY_LOCATION_ID
            var CheckIfExist1 = finalChildArray.filter(function (el) {
                return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
            });
            if (CheckIfExist1.length == 0) {
                var CheckIfExist = data.filter(function (el) {
                    return el.PARENT_LOCATION_ID == ChildfolderId;
                });
                if (CheckIfExist != null && CheckIfExist.length > 0) {
                    let rslt = ths.getChildLocation(ChildfolderId, value, data);
                    finalChildArray.push(rslt);
                } else {
                    treeArray = {
                        id: `${value.COMPANY_LOCATION_ID}`,
                        label: `${value.LOCATION_NAME}`
                    }
                    finalChildArray.push(treeArray);
                }
            }
        });
        retrunArray.children = finalChildArray;
        return retrunArray;
    },

    async BindItemTypeByUserId() {
      let url = `type=&issueTo=&locationId=&requestType=ASSIGN&requestFrom=`;
      var vm = this;
      await DataService.AssignGetAssetType(url).then((response) => {
        response.data.forEach((itm, index) => {
          vm.FormSchema[0].Data[1].config.options.push({
            name: itm.ASSETTYPE,
            value: itm.ASSETID,
          });
        });
      });
    },

    async onChangeDropdown(evt) {     
      var vm = this;
      var params = `assetTypeid=${evt.target.value}`;
      await DataService.GetAssetCatalogueType(params).then(function (response) {
        response.data.forEach((itm, index) => {
          vm.FormSchema[0].Data[2].config.options.push({
            name: itm.NAME,
            value: itm.ASSETID,
          });
        });
      });
    },

    async GetIssuedDetails() {      
      var vm = this;
      await DataService.GetIssuedUser().then(response => {
        vm.IsuuedArray=response.data.result
        vm.IsuuedArray.forEach((itm, index) => {
          vm.FormSchema[0].Data[4].config.options.push({
            name: itm.text,
            value: itm.value,
          });
        });
      });
    },
    async onSubmit(e) {        
            var vm=this;  
            vm.reportPath = '';      
            vm.parameter=[];
            const valuesArray = (vm.FormSchema)[0].Data[2].value.length >0? (vm.FormSchema)[0].Data[2].value.map(item => item.value):(vm.FormSchema)[0].Data[2].value;         
            const commaSeparatedValues=valuesArray.length > 1 ? valuesArray.join(','):valuesArray.length > 0 ? valuesArray[0]:'';    
            let myObj = {
                reportName:"assetmanagementreport",
                Location: e.Location,
                AssetTypesIds:  e.ProductType,
                CatalogueIds: commaSeparatedValues,
                StatusIds: e.Status,
                IssuedToUserIds:e.IssuedTo,
                IssuedByUserIds: e.IssuedBy,
                IsssuedFromDate: e.IssueDate,
                IsssuedToDate:e.IssueDateEnd,
                ReturnFromDate: e.ReturnDate,
                ReturnToDate: e.ReturnDateEnd,
            }          
           await DataService.ReportMain(myObj).then(function (response) {
                console.log(response);
                 vm.reportPath = response.data.reportpath;
                vm.parameter = response.data.parameters;
            })

    },
    ClearAll: function(){
      var vm=this;         
      (vm.FormSchema)[0].Data[0].value=null;
      (vm.FormSchema)[0].Data[1].value='';
      (vm.FormSchema)[0].Data[2].value='';
      (vm.FormSchema)[0].Data[3].value='';
      (vm.FormSchema)[0].Data[4].value='';
      (vm.FormSchema)[0].Data[5].value='';
      (vm.FormSchema)[0].Data[6].value='';
      (vm.FormSchema)[0].Data[7].value='';
      (vm.FormSchema)[0].Data[8].value='';      
      (vm.FormSchema)[0].Data[9].value=''; 
      vm.parameter= [];     
    },
  },
};
</script>
<style>
.vue-treeselect__control {  
    border: 1px solid #100e0e6b !important;    
}
.input-group .w-100  .d-block {
  position: absolute;
    margin-top: 37px;
    margin-left: 10px;
}
</style>